/* ==========================================================================
   Standard With Breadcrumbs Title type responsive style - begin
   ========================================================================== */

@include ipad-landscape {
	
	.edgt-title-holder {
		
		&.edgt-standard-with-breadcrumbs-type {
			
			.edgt-title-info,
			.edgt-breadcrumbs-info {
				width: 100%;
			}

            .edgt-title-info{
                display: block;
            }
			
			.edgt-breadcrumbs-info {
				margin: 14px 0 0;
				text-align: initial;
				white-space: inherit;
                position: relative;
                font-size: 12px;
			}
		}
	}
}

@include phone-portrait {

    .edgt-title-holder {

        &.edgt-standard-with-breadcrumbs-type {

            .edgt-title-info{
                margin-top: 14px;
            }

            .edgt-breadcrumbs-info {
                font-size: 12px;
            }
        }
    }
}
/* ==========================================================================
   Standard With Breadcrumbs Title type responsive style - end
   ========================================================================== */